function getAllDevelopers() {
  return new Promise((resolve) => {
    window.setTimeout(() => {
      resolve(developers)
    }, 2000)
  })
}

const getById = (id) =>
  new Promise((resolve) => {
    window.setTimeout(function () {
      resolve(developers.find((user) => user._id === id))
    }, 1000)
  })

export { getAllDevelopers, getById }
